@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;1,100;1,200;1,300;1,400&display=swap);
.lds-dual-ring{width:80px;height:80px;position:absolute;top:40%;left:50%;margin-right:-50%;transform:translate(-50%, -50%)}.lds-dual-ring:after{content:" ";display:block;width:64px;height:64px;margin:8px;border-radius:50%;border:6px solid #8254ff;border-color:#8254ff transparent #8254ff transparent;animation:lds-dual-ring 1.2s linear infinite}@keyframes lds-dual-ring{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}

.section{padding:20px}.form__edit{box-shadow:rgba(0,0,0,0.1) 0 2px 8px;width:300px;display:flex;flex-direction:column;margin:0 auto}.buttons{display:flex;padding:20px;justify-content:space-between}.form__details{display:flex;justify-content:center;padding:10px}input{padding:10px;width:90%;border:none;height:30px;margin:6px auto;display:block;border-radius:20px;box-shadow:rgba(0,0,0,0.1) 0 2px 6px;font-family:'Montserrat', sans-serif}input::-webkit-input-placeholder{color:#d2bfbf;font-size:12px}input:-ms-input-placeholder{color:#d2bfbf;font-size:12px}input::placeholder{color:#d2bfbf;font-size:12px}.form__circle{width:40px;height:40px;background-color:#d2bfbf;border-radius:100%;opacity:0.3;margin:0 4px}.btn{padding:6px;background-color:#d2bfbf;color:#fff;outline:none;cursor:pointer;width:50px;display:flex;justify-content:center;align-items:center;font-size:12px;border-radius:20px;box-shadow:rgba(0,0,0,0.1) 0 4px 12px}.btn__save{background-color:lightskyblue}.btn__save:hover{background-color:#6fc4f9}.btn__delete{background-color:#ba4b4b}.btn-add{margin:0 auto}

*{box-sizing:border-box}body{margin:0}ul,ol,h1,h2,h3,h4,h5,h6,p{margin:0;padding:0;list-style:none}button{border:0}a{text-decoration:none;color:inherit}input{outline:none}

.book{width:300px;display:flex;flex-direction:column;margin:0 auto;font-family:'Montserrat', sans-serif}.book__item{box-shadow:rgba(0,0,0,0.1) 0 4px 12px;padding:12px;position:relative;display:flex;justify-content:space-between;align-items:center;margin:6px 0}.book__name{width:100px;font-weight:bold;font-size:16px}.book__number{width:100px;font-weight:100;font-size:12px;color:#d2bfbf;padding:4px 0}

.form__add{box-shadow:rgba(0,0,0,0.1) 0 2px 8px;width:300px;display:flex;flex-direction:column;margin:0 auto}


